<template>
    <section class="has-card footer" id="training">
        <div class="container container-lg">
            <h4 class="text-center display-4 mb-5">Data Training</h4>
            <p style="font-weight: 500; margin-bottom: 4px">Berikut adalah data yang digunakan untuk proses training. Sumber <strong>Github API's</strong>.</p>
            <p style="font-weight: 500">
                Learning rate: <strong>{{ fixLr }}</strong> dengan 
                akurasi: <strong>{{ accuration }}%</strong>
            </p>
            <div class="row">
                <table class="table table-striped table-responsive">
                    <thead>
                        <th>No.</th>
                        <th>Username</th>
                        <th>HTML</th>
                        <th>CSS</th>
                        <th>JS</th>
                        <th>TS</th>
                        <th>Vue</th>
                        <th>PHP</th>
                        <th>C</th>
                        <th>Java</th>
                        <th>Ruby</th>
                        <th>Go</th>
                        <th>Python</th>
                        <th>Target</th>
                        <th>Status</th>
                    </thead>
                    <tbody>
                        <tr v-for="user in dataTested" :key="user.id">
                            <td>{{ user.id }}</td>
                            <td>{{ user.username }}</td>
                            <td>{{ user.html }}</td>
                            <td>{{ user.css }}</td>
                            <td>{{ user.javascript }}</td>
                            <td>{{ user.typescript }}</td>
                            <td>{{ user.vue }}</td>
                            <td>{{ user.php }}</td>
                            <td>{{ user.c }}</td>
                            <td>{{ user.java }}</td>
                            <td>{{ user.ruby }}</td>
                            <td>{{ user.go }}</td>
                            <td>{{ user.python }}</td>
                            <td>{{ user.target }}</td>
                            <td>{{ user.status }}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </section>
</template>

<script>
import data from '../data'
import LVQ from '../mixins/LVQ'

export default {
    mixins: [LVQ],
    data () {
        return {

        }
    },
}
</script>

<style>

</style>



