<template>
    <section class="has-card footer" id="analyze" style="background-color: #fff">
        <div class="container container-lg">
            <h4 class="text-center display-4 mb-5">Uji Data</h4>
            <p style="font-weight: 500">Masukkan username GitHub kamu untuk melihat potensi keterampilan programming yang kamu kuasai.</p>
            <div class="row">
                <div class="col-12 mb-4">
                    <div class="row">
                        <div class="col-8">
                            <input @keyup.enter="analyze" v-model="username" type="text" class="form-control" placeholder="Contoh: biobii">
                        </div>
                        <div class="col-2">
                            <button @click="analyze" class="btn btn-primary mr-2">
                                <span class="btn-inner--icon"><i class="ni ni-check-bold"></i> Analisa</span>
                            </button>
                        </div>
                    </div>
                </div>

                <div v-if="showTable" style="margin: 0 auto">
                    <img v-if="user.target === 'Frontend Programmer'" :src="showImage('frontend-dev.gif')" alt="">
                    <img v-if="user.target === 'Backend Programmer'" :src="showImage('backend-dev.png')" alt="">
                    <img v-if="user.target === 'Java Programmer'" :src="showImage('java-dev.png')" alt="">

                    <p style="font-weight: bold" class="mt-4 ml-3">{{ user.target }}</p>
                </div>

                <table class="table table-striped table-responsive mt-2" v-if="showTable">
                    <thead>
                        <th>Username</th>
                        <th>HTML</th>
                        <th>CSS</th>
                        <th>JS</th>
                        <th>TS</th>
                        <th>Vue</th>
                        <th>PHP</th>
                        <th>C</th>
                        <th>Java</th>
                        <th>Ruby</th>
                        <th>Go</th>
                        <th>Python</th>
                        <th>Hasil Analisa</th>
                    </thead>
                    <tbody>
                        <tr>
                            <td>{{ user.username }}</td>
                            <td>{{ user.html }}</td>
                            <td>{{ user.css }}</td>
                            <td>{{ user.javascript }}</td>
                            <td>{{ user.typescript }}</td>
                            <td>{{ user.vue }}</td>
                            <td>{{ user.php }}</td>
                            <td>{{ user.c }}</td>
                            <td>{{ user.java }}</td>
                            <td>{{ user.ruby }}</td>
                            <td>{{ user.go }}</td>
                            <td>{{ user.python }}</td>
                            <td>
                                <span v-if="user.target === 'Frontend Programmer'" class="badge badge-info">{{ user.target }}</span>
                                <span v-if="user.target === 'Backend Programmer'" class="badge badge-primary">{{ user.target }}</span>
                                <span v-if="user.target === 'Java Programmer'" class="badge badge-warning">{{ user.target }}</span>
                            </td>
                        </tr>
                    </tbody>
                    <br>
                    <p class="ml-4 mb-1" style="font-weight: 500">Bobot hasil:</p>
                    <p class="ml-4 mb-1">Frontend = {{ wResult.frontend }}</p>
                    <p class="ml-4 mb-1">Backend = {{ wResult.backend }}</p>
                    <p class="ml-4 mb-1">Java = {{ wResult.java }}</p>
                </table>
                <p class="ml-3 mt-3" v-if="loading">Tunggu sebentar...</p>
                <p class="ml-3 mt-3" v-if="isError" style="color:red">{{ message }}</p>
            </div>
        </div>
    </section>
</template>

<script>
import data from '../data'
import LVQ from '../mixins/LVQ'

export default {
    mixins: [LVQ],
    data () {
        return {

        }
    }
}
</script>

<style>

</style>



